"use strict";

import {Component, ViewChild, OnInit, ViewEncapsulation} from '@angular/core';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Tiers} from "../metier/tiers.metier";
import {FormGroup, FormBuilder} from "@angular/forms";
import {BaseComponent} from "../../../core/component/base-component";
import {TiersService} from "../service/tiers.service";
import 'rxjs/Rx' ;
import 'rxjs/add/operator/toPromise';
import {saveAs} from 'file-saver/FileSaver';

@Component({
  selector: 'ms-tiers-list',
  templateUrl: './tiers.list.component.html',
  styleUrls: ['./tiers.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class TiersListComponent extends BaseComponent implements OnInit {

  public tiersForm: FormGroup;
  private tiers: Tiers;
  private tiersList: Array<Tiers> = [];
  private file: Blob;

  constructor(private fb: FormBuilder,
              private tiersService: TiersService,
              private pageTitleService: PageTitleService) {
    super();
    this.createForm();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES TIERS");
    this.loadTiersList();

  }

  createForm() {
    this.tiersForm = this.fb.group({
      libelle: '',
    });
  }


  findTiersList(tiersForm: any) {

    this.tiers = new Tiers(tiersForm);
    this.tiersService.findTiersListByCriteria(this.tiers)
      .subscribe(tiersList => {
        this.tiersList = tiersList;
        this.getPdfFile();

      }, error => {

        this.showError(error.status, error.json().message);
      });
  }

  private loadTiersList() {

    this.tiersService.getTiersList()
      .subscribe(tiersList => {
        this.tiersList = tiersList;
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  private getPdfFile() {
    this.tiersService.downloadPdf()
      .subscribe(file => {
          this.file = file;
          this.downloadFile(this.file);
        }, error => this.showError(error.status, error.json().message)
      );
  }

}






