import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {CustomValidators} from 'ng2-validation';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Tiers} from "../metier/tiers.metier";
import {TiersService} from "../service/tiers.service";
import {BaseComponent} from "../../../core/component/base-component";

const password = new FormControl('', Validators.required);
const confirmPassword = new FormControl('', CustomValidators.equalTo(password));

@Component({
  selector: 'ms-tiers-add',
  templateUrl: './tiers.add.component.html',
  styleUrls: ['./tiers.add.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class TiersAddComponent extends BaseComponent implements OnInit {

  public tiersForm:FormGroup;
  private tiers:Tiers;

  constructor(private fb:FormBuilder, private pageTitleService:PageTitleService, private tiersService: TiersService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("AJOUTER UN TIER");
    this.tiersForm = this.fb.group({
      libelle: ['', Validators.required],
    });
  }

  saveTiers(tiersForm: any) {
    this.tiers = new Tiers(tiersForm);
    this.tiersService.saveTiers(this.tiers)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.reset();
        this.router.navigate(['/admin/tiers/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  reset() {
    this.tiersForm.reset();
  }


}






