import {Routes} from '@angular/router';

import {TiersAddComponent}  from './tiers.add/tiers.add.component';
import {TiersListComponent} from "./tiers.list/tiers.list.component";
import {TiersEditComponent} from "./tiers.edit/tiers.edit.component";

export const TiersRoutes:Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [{
    path: 'add', component: TiersAddComponent},
    {path: 'list', component: TiersListComponent},
    {path: 'edit/:id', component: TiersEditComponent},

  ]
}];



