import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {TiersAddComponent}  from './tiers.add/tiers.add.component';
import {TiersRoutes} from './tiers.routing';
import {TiersService} from "./service/tiers.service";
import {TiersListComponent} from "./tiers.list/tiers.list.component";
import {TiersEditComponent} from "./tiers.edit/tiers.edit.component";
import {DataTableModule} from "angular2-datatable";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(TiersRoutes)
  ],
  declarations: [
    TiersAddComponent,
    TiersListComponent,
    TiersEditComponent,
  ],
  providers:[
    TiersService,
  ],
})

export class TiersModule {
}



