import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Tiers} from "../metier/tiers.metier";
import {TiersService} from "../service/tiers.service";
import {BaseComponent} from "../../../core/component/base-component";
import {Params, ActivatedRoute} from "@angular/router";

@Component({
  selector: 'ms-tiers-edit',
  templateUrl: './tiers.edit.component.html',
  styleUrls: ['./tiers.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class TiersEditComponent extends BaseComponent implements OnInit {
  private tiers:Tiers;
  public tiersForm:FormGroup;


  constructor(private fb:FormBuilder,
              private route:ActivatedRoute,
              private pageTitleService:PageTitleService,
              private tiersService:TiersService) {
    super();
  }

  ngOnInit() {
    this.loadTiers();
    this.pageTitleService.setTitle("MODIFIER UN TIER");
    this.tiersForm = this.fb.group({
      id: [Validators.required],
      libelle: ['', Validators.required],

    });
  }


  updateTiers(tiersForm:any) {
    this.tiers = new Tiers(tiersForm);
    this.tiersService.saveTiers(this.tiers)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/admin/tiers/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  loadTiers() {
    this.route.params.switchMap((params:Params) => this.tiersService.getTiers(+params['id']))
      .subscribe(tiers => {
          this.tiers = tiers;
          this.tiersForm.patchValue(this.tiers);
        }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteTiers(tiersForm:any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.tiers = new Tiers(tiersForm);
    this.tiersService.deleteTiers(this.tiers)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/admin/tiers/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }


}






